// app/products/[slug]/landing-page/page.tsx

import LandingPageBuilder from "@/app/landing-pages/new/page"; // adjust path if needed

export default function ProductLandingPageBuilder({
  params,
}: {
  params: { slug: string };
}) {
  // Optional: If your slug is actually the numeric ID (e.g. /products/123/landing-page)
  // Then convert it to number
  const productId = Number(slug);

  // Optional validation: if slug is not a valid number
  if (isNaN(productId)) {
    return (
      <div className="flex items-center justify-center h-screen">
        <p className="text-red-600 text-xl">Invalid product ID</p>
      </div>
    );
  }

  // Pass the productId directly to your shared builder component
  return <LandingPageBuilder productId={productId} />;
}